@-webkit-keyframes stylesBattle_shake__10epJ {
    0% {
        -webkit-transform: translate(1px, 1px) rotate(0deg) translateY(-25px);
                transform: translate(1px, 1px) rotate(0deg) translateY(-25px);
    }
    10% {
        -webkit-transform: translate(-1px, -2px) rotate(-1deg) translateY(-25px);
                transform: translate(-1px, -2px) rotate(-1deg) translateY(-25px);
    }
    20% {
        -webkit-transform: translate(-3px, 0px) rotate(1deg) translateY(-25px);
                transform: translate(-3px, 0px) rotate(1deg) translateY(-25px);
    }
    30% {
        -webkit-transform: translate(3px, 2px) rotate(0deg) translateY(-25px);
                transform: translate(3px, 2px) rotate(0deg) translateY(-25px);
    }
    40% {
        -webkit-transform: translate(1px, -1px) rotate(1deg) translateY(-25px);
                transform: translate(1px, -1px) rotate(1deg) translateY(-25px);
    }
    50% {
        -webkit-transform: translate(-1px, 2px) rotate(-1deg) translateY(-25px);
                transform: translate(-1px, 2px) rotate(-1deg) translateY(-25px);
    }
    60% {
        -webkit-transform: translate(-3px, 1px) rotate(0deg) translateY(-25px);
                transform: translate(-3px, 1px) rotate(0deg) translateY(-25px);
    }
    70% {
        -webkit-transform: translate(3px, 1px) rotate(-1deg) translateY(-25px);
                transform: translate(3px, 1px) rotate(-1deg) translateY(-25px);
    }
    80% {
        -webkit-transform: translate(-1px, -1px) rotate(1deg) translateY(-25px);
                transform: translate(-1px, -1px) rotate(1deg) translateY(-25px);
    }
    90% {
        -webkit-transform: translate(1px, 2px) rotate(0deg) translateY(-25px);
                transform: translate(1px, 2px) rotate(0deg) translateY(-25px);
    }
    100% {
        -webkit-transform: translate(1px, -2px) rotate(-1deg) translateY(-25px);
                transform: translate(1px, -2px) rotate(-1deg) translateY(-25px);
    }
}

@keyframes stylesBattle_shake__10epJ {
    0% {
        -webkit-transform: translate(1px, 1px) rotate(0deg) translateY(-25px);
                transform: translate(1px, 1px) rotate(0deg) translateY(-25px);
    }
